"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useAuth } from "@/hooks/useAuth";
import { getPatient, type PatientWithId } from "@/utils/firestore/patients";
import {
  getReports,
  updateReport,
  deleteReport,
  type ReportWithId,
} from "@/utils/firestore/reports";
import { toast } from "sonner";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  Drawer,
  DrawerContent,
  DrawerDescription,
  DrawerFooter,
  DrawerHeader,
  DrawerTitle,
  DrawerTrigger,
} from "@/components/ui/drawer";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import {
  Loader2,
  Edit,
  Trash2,
  FileText,
  ArrowLeft,
} from "lucide-react";
import Link from "next/link";
// Helper function to format date
const formatDate = (date: Date): string => {
  return date.toLocaleDateString("en-US", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

const reportSchema = z.object({
  date: z.date({
    required_error: "Date is required",
  }),
  doctor: z.string().min(1, "Doctor name is required"),
  tests: z.record(
    z.record(z.union([z.number(), z.string()]))
  ),
  comments: z.string().optional(),
});

type ReportFormValues = z.infer<typeof reportSchema>;

export default function ReportsPage() {
  const params = useParams();
  const router = useRouter();
  const patientId = params.patientId as string;
  const { user: firebaseUser, loading: authLoading } = useAuth();
  const [patient, setPatient] = useState<PatientWithId | null>(null);
  const [reports, setReports] = useState<ReportWithId[]>([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [deleting, setDeleting] = useState<string | null>(null);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [editingReport, setEditingReport] = useState<ReportWithId | null>(
    null
  );

  const form = useForm<ReportFormValues>({
    resolver: zodResolver(reportSchema),
    defaultValues: {
      date: new Date(),
      doctor: "Self",
      tests: {},
      comments: "",
    },
  });

  useEffect(() => {
    if (!firebaseUser?.uid || !patientId) {
      setLoading(false);
      return;
    }

    fetchData();
  }, [firebaseUser?.uid, patientId]);

  const fetchData = async () => {
    if (!firebaseUser?.uid || !patientId) return;

    try {
      setLoading(true);
      const [patientData, reportsData] = await Promise.all([
        getPatient(firebaseUser.uid, patientId),
        getReports(firebaseUser.uid, patientId),
      ]);

      if (!patientData) {
        toast.error("Patient not found");
        router.push("/dashboard/patients");
        return;
      }

      setPatient(patientData);
      setReports(reportsData);
    } catch (error) {
      console.error("Failed to fetch data:", error);
      toast.error("Failed to load data");
    } finally {
      setLoading(false);
    }
  };

  const onSubmit = async (values: ReportFormValues) => {
    if (!firebaseUser?.uid || !patientId || !editingReport) {
      toast.error("User not authenticated or report not found");
      return;
    }

    try {
      setSaving(true);
      await updateReport(
        firebaseUser.uid,
        patientId,
        editingReport.id,
        values
      );
      toast.success("Report updated successfully");
      setDrawerOpen(false);
      form.reset({
        date: new Date(),
        doctor: "Self",
        tests: {},
        comments: "",
      });
      setEditingReport(null);
      await fetchData();
    } catch (error) {
      console.error("Failed to save report:", error);
      toast.error("Failed to save report");
    } finally {
      setSaving(false);
    }
  };

  const handleEdit = (report: ReportWithId) => {
    setEditingReport(report);
    form.reset({
      date: report.date.toDate(),
      doctor: report.doctor,
      tests: report.tests,
      comments: report.comments || "",
    });
    setDrawerOpen(true);
  };

  const handleDelete = async (reportId: string) => {
    if (!firebaseUser?.uid || !patientId) {
      toast.error("User not authenticated");
      return;
    }

    try {
      setDeleting(reportId);
      await deleteReport(firebaseUser.uid, patientId, reportId);
      toast.success("Report deleted successfully");
      await fetchData();
    } catch (error) {
      console.error("Failed to delete report:", error);
      toast.error("Failed to delete report");
    } finally {
      setDeleting(null);
    }
  };

  const handleDrawerOpenChange = (open: boolean) => {
    setDrawerOpen(open);
    if (!open) {
      form.reset({
        date: new Date(),
        doctor: "Self",
        tests: {},
        comments: "",
      });
      setEditingReport(null);
    }
  };

  if (authLoading || loading) {
    return (
      <div className="space-y-6">
        <div>
          <Skeleton className="h-8 w-48" />
          <Skeleton className="h-4 w-96 mt-2" />
        </div>
        <Card>
          <CardHeader>
            <Skeleton className="h-6 w-48" />
            <Skeleton className="h-4 w-96" />
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (!firebaseUser || !patient) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] space-y-4">
        <FileText className="h-12 w-12 text-muted-foreground" />
        <h2 className="text-2xl font-semibold">Not Found</h2>
        <p className="text-muted-foreground">
          Patient or reports not found
        </p>
        <Button asChild variant="outline">
          <Link href="/dashboard/patients">
            <ArrowLeft className="mr-2 h-4 w-4" />
            Back to Patients
          </Link>
        </Button>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <div className="flex items-center gap-4 mb-2">
            <Button asChild variant="outline" size="icon">
              <Link href="/dashboard/patients">
                <ArrowLeft className="h-4 w-4" />
              </Link>
            </Button>
            <div>
              <h1 className="text-3xl font-bold tracking-tight">
                Reports - {patient.name}
              </h1>
              <p className="text-muted-foreground">
                View and manage reports for {patient.name}
              </p>
            </div>
          </div>
        </div>
        <Drawer open={drawerOpen} onOpenChange={handleDrawerOpenChange}>
          <DrawerContent>
            <div className="mx-auto w-full max-w-sm">
              <DrawerHeader>
                <DrawerTitle>Edit Report</DrawerTitle>
                <DrawerDescription>
                  Update report information below.
                </DrawerDescription>
              </DrawerHeader>
              <div className="p-4 pb-0 max-h-[70vh] overflow-y-auto">
                <Form {...form}>
                  <form
                    onSubmit={form.handleSubmit(onSubmit)}
                    className="space-y-4"
                  >
                    <FormField
                      control={form.control}
                      name="date"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Date</FormLabel>
                          <FormControl>
                            <Input
                              type="date"
                              value={
                                field.value
                                  ? field.value.toISOString().split("T")[0]
                                  : ""
                              }
                              onChange={(e) =>
                                field.onChange(new Date(e.target.value))
                              }
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="doctor"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Doctor</FormLabel>
                          <FormControl>
                            <Input
                              placeholder="Enter doctor name"
                              {...field}
                            />
                          </FormControl>
                          <FormDescription>
                            Default: Self
                          </FormDescription>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="comments"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Comments</FormLabel>
                          <FormControl>
                            <Textarea
                              placeholder="Enter comments (optional)"
                              rows={3}
                              {...field}
                            />
                          </FormControl>
                          <FormDescription>
                            Additional notes or comments
                          </FormDescription>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <div className="text-sm text-muted-foreground">
                      <p className="font-medium mb-2">Tests</p>
                      <p className="text-xs">
                        Test results can be added in a future update. For now,
                        the tests field is stored as structured data.
                      </p>
                    </div>

                    <DrawerFooter>
                      <Button type="submit" disabled={saving}>
                        {saving && (
                          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        )}
                        {saving ? "Saving..." : "Update"}
                      </Button>
                      <Button
                        type="button"
                        variant="outline"
                        onClick={() => handleDrawerOpenChange(false)}
                        disabled={saving}
                      >
                        Cancel
                      </Button>
                    </DrawerFooter>
                  </form>
                </Form>
              </div>
            </div>
          </DrawerContent>
        </Drawer>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Patient Reports</CardTitle>
          <CardDescription>
            A list of all reports for {patient.name}. You can edit or delete
            records.
          </CardDescription>
        </CardHeader>
        <CardContent>
          {reports.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-12 text-center">
              <FileText className="h-12 w-12 text-muted-foreground mb-4" />
              <h3 className="text-lg font-semibold mb-2">No reports found</h3>
              <p className="text-muted-foreground">
                Reports will appear here once they are created.
              </p>
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Date</TableHead>
                  <TableHead>Doctor</TableHead>
                  <TableHead>Tests</TableHead>
                  <TableHead>Comments</TableHead>
                  <TableHead className="text-right">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {reports.map((report) => (
                  <TableRow key={report.id}>
                    <TableCell className="font-medium">
                      {formatDate(report.date.toDate())}
                    </TableCell>
                    <TableCell>{report.doctor}</TableCell>
                    <TableCell>
                      {Object.keys(report.tests).length > 0
                        ? `${Object.keys(report.tests).length} test(s)`
                        : "No tests"}
                    </TableCell>
                    <TableCell>
                      {report.comments || "-"}
                    </TableCell>
                    <TableCell className="text-right">
                      <div className="flex items-center justify-end gap-2">
                        <Button
                          variant="outline"
                          size="icon"
                          onClick={() => handleEdit(report)}
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                        <AlertDialog>
                          <AlertDialogTrigger asChild>
                            <Button
                              variant="outline"
                              size="icon"
                              disabled={deleting === report.id}
                            >
                              {deleting === report.id ? (
                                <Loader2 className="h-4 w-4 animate-spin" />
                              ) : (
                                <Trash2 className="h-4 w-4" />
                              )}
                            </Button>
                          </AlertDialogTrigger>
                          <AlertDialogContent>
                            <AlertDialogHeader>
                              <AlertDialogTitle>Are you sure?</AlertDialogTitle>
                              <AlertDialogDescription>
                                This action cannot be undone. This will
                                permanently delete the report from{" "}
                                <strong>
                                  {formatDate(report.date.toDate())}
                                </strong>
                                .
                              </AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                              <AlertDialogCancel>Cancel</AlertDialogCancel>
                              <AlertDialogAction
                                onClick={() => handleDelete(report.id)}
                                className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                              >
                                Delete
                              </AlertDialogAction>
                            </AlertDialogFooter>
                          </AlertDialogContent>
                        </AlertDialog>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

